<template>
  <div id="index-root">
    <div id="index-content">
      <ChatBox
        @showKG="showKG"
        @showMap="showMap"
        @showRuleTips="showRuleTips"
        ref="child"
      ></ChatBox>
    </div>

    <div id="index-popup" v-if="isShowGraph">
      <div id="index-popup-close">
        <el-button
          type="danger"
          icon="el-icon-close"
          @click="closeKG"
        ></el-button>
      </div>
      <div id="index-popup-content">
        <Graph></Graph>
      </div>
    </div>

    <div id="index-popup" v-if="isShowMap">
      <!-- <div id="index-graph-close">
        <el-button icon="el-icon-close" @click="closeMap"></el-button>
      </div> -->
      <div id="index-popup-content">
        <Map :map_data="map_data"></Map>
      </div>
      <div id="index-popup-close" @click="closeMap">
        <p>Close</p>
      </div>
    </div>

    <div id="index-popup" v-if="isShowRuleTips">
      <!-- <div id="index-graph-close">
        <el-button icon="el-icon-close" @click="closeMap"></el-button>
      </div> -->
      <div id="index-popup-ruleSearch">
        <div>
          <el-input
            v-model="search"
            placeholder="Please Input Keywords"
          ></el-input>
        </div>
      </div>
      <div id="index-popup-ruleContent">
        <ul>
          <li v-for="(item, index) in searchData" :key="index" @click="sendEntity($event,item)">
            {{ item }}
          </li>
        </ul>
      </div>
      <div id="index-popup-close" @click="closeRuleTips">
        <p>Close</p>
      </div>
    </div>
  </div>
</template>

<script>
import ChatBox from "../components/ChatBox.vue";
import Graph from "../components/Graph.vue";
import Map from "../components/Map.vue";
export default {
  name: "index",
  data() {
    return {
      activeIndex: "1",
      isShowGraph: false,
      isShowMap: false,
      isShowRuleTips: false,
      map_data: [],
      ruleContent: [],
      search: "",
    };
  },
  components: {
    ChatBox,
    Graph,
    Map,
  },
  methods: {
    closeKG() {
      this.isShowGraph = false;
    },
    showKG() {
      this.isShowGraph = true;
    },
    showMap(childValue) {
      this.map_data = childValue;
      this.isShowMap = true;
    },
    closeMap() {
      this.isShowMap = false;
    },
    showRuleTips(value) {
      this.ruleContent = value;
      this.isShowRuleTips = true;
    },
    closeRuleTips() {
      this.isShowRuleTips = false;
    },
    sendEntity(e,value){
      e.preventDefault();
      this.isShowRuleTips = false;
      this.$refs.child.send(value);
    }
  },
  computed: {
    searchData: function () {
      var search = this.search;

      if (search) {
        return this.ruleContent.filter(function (item) {
          if (String(item.toLowerCase().indexOf(search)) > -1) {
            return true;
          } else {
            return false;
          }
        });
      }else{
        return this.ruleContent;
      }
    },
  },
};
</script>

