<template>
  <div class="amap" ref="container"></div>
</template>
<script>
import AMapLoader from "@amap/amap-jsapi-loader";

export default {
  name: "map",
  props: ["map_data"],
  created() {
    window._AMapSecurityConfig = {
      securityJsCode: "d916527561290764f04b7284c408348f",
    };
    AMapLoader.load({
      key: "49ff7cf3a2f52dafb09a6099ac289c22", // 申请好的Web端开发者Key，首次调用 load 时必填
      version: "1.4.15", // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
      plugins: ["AMap.ToolBar"], // 需要使用的的插件列表，如比例尺'AMap.Scale'等
      AMapUI: {
        // 是否加载 AMapUI，缺省不加载
        version: "1.1", // AMapUI 缺省 1.1
        plugins: [], // 需要加载的 AMapUI ui插件
      },
      Loca: {
        // 是否加载 Loca， 缺省不加载
        version: "1.3.2", // Loca 版本，缺省 1.3.2
      },
    })
      .then((AMap) => {
        this.$nextTick(() => this.initMap(AMap));
      })
      .catch((e) => {
        console.error(e);
      });
  },
  methods: {
    initMap(AMap) {
      // this.map = new AMap.Map("container");
      // 或者使用 $refs 获取节点
      this.map = new AMap.Map(this.$refs.container, {
        zoom: 18,
        pitch: 80, // 地图俯仰角度，有效范围 0 度- 83 度
        // viewMode: "3D", // 地图模式
        // terrain: true, // 开启地形图
        // rotation: -15, // 地图顺时针旋转角度，取值范围 [0-360] ，默认值：0
        buildingAnimation: true, //楼块出现是否带动画
        expandZoomRange: true,
        // center: [114.17891001918079, 22.303519892980272],
        zooms: [3, 18], //地图显示的缩放级别范围
        lang: "zh_en", //设置地图语言类型
        features: ["building", "bg", "point"], // 设置地图上显示的元素种类
      });

      let that = this;
      AMap.plugin("AMap.ToolBar", function () {
        //异步加载插件
        var toolbar = new AMap.ToolBar();
        that.map.addControl(toolbar);
      });

      let avg_lng = 0;
      let avg_lat = 0;
      let func = function (element) {
        return new Promise(function (resolve, reject) {
          let gps = [element[1][1], element[1][0]];
          AMap.convertFrom(gps, "gps", function (status, result) {
            if (result.info === "ok") {
              var lng = result.locations[0].lng;
              var lat = result.locations[0].lat;

              avg_lng += lng;
              avg_lat += lat;

              // that.map.setCenter([lng, lat]);

              // 添加标记
              var marker = new AMap.Marker({
                position: new AMap.LngLat(lng, lat), // 经纬度
              });
              marker.setLabel({
                offset: new AMap.Pixel(0, 0), //设置文本标注偏移量
                content: "<h3>" + element[0] + "</h3>", //设置文本标注内容
                direction: "top", //设置文本标注方位
              });
              that.map.add(marker);
              resolve();
            }
          });
        });
      };

      let test = async function () {
        for (let i = 0; i < that.map_data.length; i++) {
          let element = that.map_data[i];
          await func(element);
          if (i == that.map_data.length - 1) {
            console.log(avg_lng, avg_lat);
            that.map.setCenter([
              avg_lng / that.map_data.length,
              avg_lat / that.map_data.length,
            ]);
          }
        }
      };

      test();

      let gps = [114.17891001918079, 22.303519892980272];
      AMap.convertFrom(gps, "gps", function (status, result) {
        if (result.info === "ok") {
          var lng = result.locations[0].lng;
          var lat = result.locations[0].lat;

          that.map.setCenter([lng, lat]);

          // 添加标记
          //   var marker = new AMap.Marker({
          //     position: new AMap.LngLat(lng, lat), // 经纬度
          //   });

          //   that.map.add(marker);
        }
      });

      // AMap.plugin("AMap.Geocoder", function () {
      //   var geocoder = new AMap.Geocoder();
      //   geocoder.getLocation("香港理工大学何鸿燊楼", function (status, result) {
      //     if (status === "complete" && result.info === "OK") {
      //       // 经纬度
      //       var lng = result.geocodes[0].location.lng;
      //       var lat = result.geocodes[0].location.lat;

      //       that.map.setCenter([lng, lat]);

      //       // 添加标记
      //       var marker = new AMap.Marker({
      //         position: new AMap.LngLat(lng, lat), // 经纬度
      //       });

      //       that.map.add(marker);
      //     } else {
      //       console.log("定位失败！");
      //     }
      //   });
      // });
    },
  },
};
</script>
<style>
.amap {
  width: 100%;
  height: 100%;
}
</style>